.ParksContainer {
  display: flex;
  flex-wrap: wrap;
  background-image: url("../../assets/images/topo.png");
  min-height: 87vh;
}

.favorites {
  background-image: url("../../assets/images/topo-gray.png");
}

.Favorites-star {
  height: 30px;
  padding-right: 3px;
  transition: transform .75s ease-in-out;
  vertical-align: bottom;
}

.Favorites-star:hover {
  transform: rotate(90deg);
}

.Park-link {
  background-color: #F5F5F5;
  border: .5px solid #989898;
  border-radius: 3px;
  display: block;
  font-family: "Roboto Slab";
  height: 250px;
  margin: 10px;
  object-fit: cover;
  padding: 10px;
  text-align: center;
  width: 300px;
}

.Park-link-heading {
  color: black;
  font-size: 21px;
  margin: 1px;
}

.Park-link-body {
  color: #585858;
  font-size: 16px;
  margin: 10px;
}

.Park-link-description {
  color: #585858;
  font-size: 12px;
  max-height: 50px;
  overflow: hidden;
}

.Park-link-ellipsis {
  color: black;
  font-size: 36px;
  line-height: 0px;
  margin: 0;
}

.Park-link-more {
  color: #1f4045;
  font-size: 16px;
  line-height: 6;
}